.hero {
    position: relative;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background: var(--bg-primary);
}

.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.grid {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image:
        linear-gradient(rgba(255, 255, 255, 0.03) 1px, transparent 1px),
        linear-gradient(90deg, rgba(255, 255, 255, 0.03) 1px, transparent 1px);
    background-size: 50px 50px;
    mask-image: radial-gradient(circle at center, black 40%, transparent 80%);
}

.orb {
    position: absolute;
    border-radius: 50%;
    filter: blur(80px);
    opacity: 0.4;
}

.orb1 {
    width: 400px;
    height: 400px;
    background: #2a2a2a;
    /* Deep grey/blue placeholder */
    top: 20%;
    left: 20%;
}

.orb2 {
    width: 600px;
    height: 600px;
    background: #1a1a1a;
    bottom: 10%;
    right: 10%;
}

.content {
    position: relative;
    z-index: 1;
    text-align: center;
    max-width: 800px;
    padding: 0 20px;
}

.title {
    font-size: clamp(3rem, 8vw, 6rem);
    font-weight: 700;
    line-height: 1.1;
    letter-spacing: -0.04em;
    margin-bottom: 2rem;
    background: linear-gradient(to bottom, #fff, #888);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.highlight {
    color: #fff;
    font-weight: 300;
    font-style: italic;
    -webkit-text-fill-color: initial;
}

.subtitle {
    font-size: clamp(1rem, 2vw, 1.25rem);
    color: var(--text-secondary);
    font-weight: 400;
    letter-spacing: -0.01em;
    max-width: 600px;
    margin: 0 auto;
}

/* Floating Hero Image - Particle-like effect */
.floatingImage {
    position: absolute;
    bottom: 8%;
    right: 5%;
    z-index: 2;
    will-change: transform, opacity;
    pointer-events: none;
}

.heroImage {
    width: auto;
    height: auto;
    max-width: 450px;
    filter: drop-shadow(0 0 30px rgba(255, 107, 107, 0.3)) drop-shadow(0 0 60px rgba(100, 149, 237, 0.2));
    opacity: 0.95;
}

/* Responsive adjustments */
@media (max-width: 1200px) {
    .floatingImage {
        right: 2%;
        bottom: 5%;
    }

    .heroImage {
        max-width: 350px;
    }
}

@media (max-width: 768px) {
    .floatingImage {
        position: relative;
        bottom: auto;
        right: auto;
        margin-top: 3rem;
        display: flex;
        justify-content: center;
    }

    .heroImage {
        max-width: 280px;
    }
}

@media (max-width: 480px) {
    .heroImage {
        max-width: 220px;
    }
}
